import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {LoggingService} from './logging.service';
import {Observable} from 'rxjs';
import {income_summary_routes} from '../helpers/env-endpoints';

@Injectable({
    providedIn: 'root'
})
export class IncomeSummaryService {


    private httpOptions = {
        headers: new HttpHeaders({
            'Content-Type': 'application/json'
        })
    };

    constructor(private http: HttpClient, private loggingService: LoggingService) {
    }

    searchIncomeSummary(instrumentTypeId: number, startDate: Date, endDate: Date): Observable<any> {

        const requestUrl = '/instrumentTypeId/' + instrumentTypeId + '/from/' + startDate.toDateString() + '/to/' + endDate.toDateString();

        console.log(income_summary_routes.INCOME_SUMMARY + requestUrl);

        this.loggingService.log('request_url', requestUrl);

        return this.http.get<any>(income_summary_routes.INCOME_SUMMARY + requestUrl);

    }
}
