import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {LoggingService} from './logging.service';
import {Observable} from 'rxjs';
import {Instrument} from '../instruments/instrument.model';
import {bonds_calculations_routes} from '../helpers/env-endpoints';
import {BondsCalculationDTO} from '../dto/bondsCalculationDTO.model';
import {Bank} from '../banks/bank.model';
import {environment} from '../../environments/environment';

@Injectable({
    providedIn: 'root'
})
export class BondsCalculationsService {

    private httpOptions = {
        headers: new HttpHeaders({
            'Content-Type': 'application/json'
        })
    };

    constructor(private http: HttpClient, private loggingService: LoggingService) {
    }

    calculateBond(dto: BondsCalculationDTO): Observable<any> {

        this.loggingService.log(dto, 'bond calculation');

        return this.http.post(bonds_calculations_routes.COMPUTE_BOND, dto, this.httpOptions);
    }
}
