import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {LoggingService} from './logging.service';
import {Observable} from 'rxjs';
import {Instrument} from '../instruments/instrument.model';
import {institution_routes} from '../helpers/env-endpoints';
import {Institution} from '../institutions/institution.model';

@Injectable({
    providedIn: 'root'
})
export class InstitutionService {

    private httpOptions = {
        headers: new HttpHeaders({
            'Content-Type': 'application/json'
        })
    };

    constructor(private http: HttpClient, private loggingService: LoggingService) {
    }

    getInstitutions(): Observable<Institution[]> {

        return this.http.get<Institution[]>(institution_routes.GET_ALL_INSTITUTIONS);
    }

    getInstitutionById(id: number): Observable<any> {

        return this.http.get<any>(institution_routes.GET_INSTITUTION_BY_ID + '/' + id);
    }

    createInstitution(institution: Institution): Observable<any> {

        this.loggingService.log(institution.name, 'added');

        return this.http.post(institution_routes.CREATE_INSTITUTION, institution, this.httpOptions);


    }

    updateInstitution(institution: Institution): Observable<any> {

        this.loggingService.log(institution.name, 'updated');

        return this.http.post(institution_routes.UPDATE_INSTITUTION, institution, this.httpOptions);


    }
}
