import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {LoggingService} from './logging.service';
import {Observable} from 'rxjs';
import {Bank} from '../banks/bank.model';
import {environment} from '../../environments/environment';
import {Instrument} from '../instruments/instrument.model';
import {trade_routes} from '../helpers/env-endpoints';
import {Trade} from '../trades/trade.model';

@Injectable({
    providedIn: 'root'
})
export class TradeService {

    private httpOptions = {
        headers: new HttpHeaders({
            'Content-Type': 'application/json'
        })
    };


    constructor(private http: HttpClient, private loggingService: LoggingService) {

    }

    getAllTrades(): Observable<Trade[]> {
        return this.http.get<Trade[]>(trade_routes.GET_ALL_TRADES);
    }

    getTradeById(id: number): Observable<any> {
        return this.http.get<Trade>(trade_routes.GET_TRADE_BY_TRADE_ID + '/' + id);
    }

    createTrade(trade: Trade): Observable<any> {

        this.loggingService.log(trade, 'added');

        return this.http.post(trade_routes.POST_TRADE, trade, this.httpOptions);
    }

    updateTrade(id: number, trade: Trade): Observable<any> {

        this.loggingService.log(id, 'trade_id');
        this.loggingService.log(trade, 'updated');

        return this.http.post(trade_routes.UPDATE_TRADE + '/' + id, trade, this.httpOptions);
    }

}
