import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {LoggingService} from './logging.service';
import {Trade} from '../trades/trade.model';
import {Observable} from 'rxjs';
import {blotter_routes, institution_routes} from '../helpers/env-endpoints';
import {BlotterRequestDtoModel} from 'app/dto/blotter-request-dto-model';
import {Institution} from '../institutions/institution.model';

@Injectable({
    providedIn: 'root'
})
export class BlotterService {

    private httpOptions = {
        headers: new HttpHeaders({
            'Content-Type': 'application/json'
        })
    };

    constructor(private http: HttpClient, private loggingService: LoggingService) {
    }

    getTradeBlotter(requestDto: BlotterRequestDtoModel): Observable<any> {

        this.loggingService.log(requestDto, 'blotter request made');

        return this.http.post(blotter_routes.BLOTTER_TRADE_REPORTS, requestDto, this.httpOptions);
    }


    getBlotterSummary(instrumentTypeId: number, startDate: Date, endDate: Date): Observable<any> {

        const requestUrl = '/instrumentTypeId/' + instrumentTypeId + '/from/' + startDate.toDateString() + '/to/' + endDate.toDateString();

        console.log(blotter_routes.BLOTTER_SUMMARY + requestUrl);

        return this.http.get<any>(blotter_routes.BLOTTER_SUMMARY + requestUrl);
    }

    searchBlotterSummary(instrumentTypeId: number, instrumentId: number, startDate: Date, endDate: Date): Observable<any> {

        const requestUrl = '/instrumentTypeId/' + instrumentTypeId + '/instrumentId/' + instrumentId + '/from/' + startDate.toDateString() + '/to/' + endDate.toDateString();

        console.log(blotter_routes.SEARCH_BLOTTER_SUMMARY + requestUrl);

        return this.http.get<any>(blotter_routes.SEARCH_BLOTTER_SUMMARY + requestUrl);
    }
}
