import {Injectable} from '@angular/core';
import {instrument_routes} from '../helpers/env-endpoints';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {LoggingService} from './logging.service';
import {Observable} from 'rxjs';
import {environment} from '../../environments/environment';
import {Instrument} from '../instruments/instrument.model';
import {Bank} from '../banks/bank.model';

@Injectable({
    providedIn: 'root'
})
export class InstrumentService {

    private httpOptions = {
        headers: new HttpHeaders({
            'Content-Type': 'application/json'
        })
    };

    constructor(private http: HttpClient, private loggingService: LoggingService) {
    }

    getInstruments(): Observable<Instrument[]> {

        return this.http.get<Instrument[]>(instrument_routes.GET_ALL_INSTRUMENTS);
    }


    updateAsyncInstrument(instrument: Instrument): Observable<any> {

        this.loggingService.log(instrument.name, 'updating..');

        return this.http.post(instrument_routes.UPDATE_INSTRUMENT, instrument, this.httpOptions);

    }
    createInstrument(instrument: Instrument): Observable<any> {

        console.log(instrument_routes.CREATE_INSTRUMENT);

        this.loggingService.log(instrument.name, 'added');

        return this.http.post(instrument_routes.CREATE_INSTRUMENT, instrument, this.httpOptions);


    }

    getInstrumentsByTypeId(instrumentTypeId: any) {
        const url = instrument_routes.GET_INSTRUMENTS_BY_TYPE_ID + '/' + instrumentTypeId.toString();
        return this.http.get<Instrument[]>(url);
    }

    getInstrumentById(instrumentId: any) {
        const url = instrument_routes.GET_INSTRUMENT_BY_ID + '/' + instrumentId.toString();
        return this.http.get<any>(url);
    }
}
