import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {LoggingService} from './logging.service';
import {BondsCalculationDTO} from '../dto/bondsCalculationDTO.model';
import {Observable} from 'rxjs';
import {treasuryBills_calculations_routes} from '../helpers/env-endpoints';
import {TreasuryBillCalculationDTO} from '../dto/treasuryBillCalculationDTO.model';

@Injectable({
    providedIn: 'root'
})
export class TreasuryBillCalculationsService {

    private httpOptions = {
        headers: new HttpHeaders({
            'Content-Type': 'application/json'
        })
    };

    constructor(private http: HttpClient, private loggingService: LoggingService) {
    }

    calculatePurchaseConsideration(dto: TreasuryBillCalculationDTO): Observable<any> {

        this.loggingService.log(dto, 'purchase consideration calculation');

        return this.http.post(treasuryBills_calculations_routes.CALCULATE_PURCHASE_CONSIDERATION, dto, this.httpOptions);

    }

    calculateSalesConsideration(dto: TreasuryBillCalculationDTO): Observable<any> {

        this.loggingService.log(dto, 'purchase sales calculation');

        return this.http.post(treasuryBills_calculations_routes.CALCULATE_SALES_CONSIDERATION, dto, this.httpOptions);
    }
}
