import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {LoggingService} from './logging.service';
import {Observable} from 'rxjs';
import {Institution} from '../institutions/institution.model';
import {institution_contact_routes, institution_routes} from '../helpers/env-endpoints';
import {InstitutionContact} from '../models/institution.contact.model';

@Injectable({
  providedIn: 'root'
})
export class InstitutionContactService {

  private httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json'
    })
  };

  constructor(private http: HttpClient, private loggingService: LoggingService) {
  }

  getInstitutionContacts(): Observable<any> {

    return this.http.get<any>(institution_contact_routes.GET_ALL_INSTITUTION_CONTACTS);
  }

  getInstitutionContactById(id: number): Observable<any> {

    return this.http.get<any>(institution_contact_routes.GET_INSTITUTION_CONTACT_BY_ID + '/' + id);
  }

  createInstitutionContact(institution: InstitutionContact): Observable<any> {

    this.loggingService.log(institution.fullname, 'added');

    return this.http.post(institution_contact_routes.CREATE_INSTITUTION_CONTACT, institution, this.httpOptions);


  }

  updateInstitutionContact(institution: InstitutionContact): Observable<any> {

    this.loggingService.log(institution.fullname, 'updated');

    return this.http.post(institution_contact_routes.UPDATE_INSTITUTION_CONTACT, institution, this.httpOptions);


  }
}
