import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {LoggingService} from './logging.service';
import {Bank} from '../banks/bank.model';
import {Observable, of} from 'rxjs';
import {environment} from '../../environments/environment';

@Injectable({
    providedIn: 'root'
})
export class SettlementBankService {

    private httpOptions = {
        headers: new HttpHeaders({
            'Content-Type': 'application/json'
        })
    };


    constructor(private http: HttpClient, private loggingService: LoggingService) {

    }

    getBankById(id: number): Observable<any> {
        const url = environment.api_base + '/SettlementBanks/getBankById/' + id;
        console.log(url);
        return this.http.get<any>(url);
    }

    getSettlementBanks(): Observable<Bank[]> {
        const url = environment.api_base + '/SettlementBanks/GetBanks'
        console.log(url);
        return this.http.get<Bank[]>(url);
    }

    private handleError<T>(operation = 'operation', result?: T) {
        return (error: any): Observable<T> => {

            console.error(error); // log to console instead

            console.log(`${operation} failed: ${error.message}`);

            // Let the app keep running by returning an empty result.
            return of(result as T);
        };
    }


    updateSettlementBank(settlementBank: Bank): Observable<any> {

        this.loggingService.log(settlementBank.name, 'updated');
        const post_url = environment.api_base + '/SettlementBanks/UpdateSettlementBank';

        return this.http.post(post_url, settlementBank, this.httpOptions);


    }

    addSettlementBank(settlementBank: Bank): Observable<any> {

        this.loggingService.log(settlementBank.name, 'added');
        const post_url = environment.api_base + '/SettlementBanks';

        return this.http.post(post_url, settlementBank, this.httpOptions);


    }

}
